@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.MainObjItem {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 20px;
  border-radius: 8px;
  background-color: #f3f3f3;
  margin-top: 20px;
}
.StaticElement {
  font-family: "Roboto", sans-serif;
  color: #bdbdbd;
  font-weight: 400;
  font-size: 15px;
  line-height: 13px;
  cursor: default;
}
.ChangingElement {
  padding-left: 10px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 13px;
  color: #000000;
}
.LastElement {
  display: flex;
  width: 95%;
  flex-direction: row;
  justify-content: space-between;
}
.Link a {
  text-decoration: none;
  cursor: pointer;
  color: #4b51ef;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 14px;
}
